
import { ethers } from 'ethers';
import React from 'react';
import './App.css';
import Trocchi from './Trocchi.png'

class App extends React.Component {
constructor(){
  super();
  this.state = {account:"still you didn't connect any wallet!"};
}
isConnect = async()=>{
let provider = new ethers.providers.Web3Provider(window.ethereum);
let accounts = await provider.send("eth_requestAccounts",[]);
if(accounts.length>0){
  return{
    status:true,
    userAccount: accounts[0],
    accountLength: accounts.length
  }
}
else{
  return{
    status:false,
    userAccount:"still not connected!"
  }
}
}

connectWallet = async()=>{
  let isConnected = await this.isConnect();
  if(isConnected.status === true){
    window.localStorage.setItem("isConnected",isConnected.status)
    this.setState({account:isConnected.userAccount})
  }
  else{
    alert("Kindly install your metamask...if it is there please connect it!")
  }
}

componentDidMount(){
  window.ethereum.on("accountsChanged",async()=>{
   window.localStorage.removeItem("isConnected");
   this.setState({account:""});
   this.connectWallet();
  })

  let localValue = localStorage.getItem("isConnected");
  if(localValue){
    this.connectWallet();
  }
  if(localValue === null){
    if(window.ethereum.selectedAddress === null){
 console.log("welcome user")
    }
    else{
      this.connectWallet();
    }
  }
}

render(){
  return(
    <>
<nav className='navbar navbar-expand-lg navbar-light bg-light'>
<div className='container-fluid'>
  <a className="navbar-brand" href="/">
      <img src={Trocchi} alt="here your logo" width="40" height="34" className="d-inline-block align-text-center"/>
     <b className='ms-1'>ZethTO</b>
    </a>
    {localStorage.getItem("isConnected")? <button className='ms-auto btn btn-primary'>Connected</button>:<button onClick={this.connectWallet} className="ms-auto btn btn-outline-primary">connect metamask</button> }
    </div>

  </nav>    
  <div>
    <hr />
    <small className='text-wrap ms-2'>this is your connected account: {this.state.account}</small>
    <hr />
    </div>
    
    </>
  )
}
}

export default App;
