import { ethers } from "ethers";
import React, {useState } from "react";
//import abi from "./factory.json"
import './App.css';
export default function Tokenmaker(){
    let [token,setToken] = useState({
        tokenName:"",
        tokenSym:"",
        tokenSupply:0
    })
    let [address,setAddress] = useState("")
 let [account,setAccount] = useState("")
   

 //coin making function
    let tokenmaker = async(event)=>{
      event.preventDefault();
if(token.tokenName !==""){
  if(token.tokenName !==""){
    if(token.tokenSym !==""){
      let provider = new ethers.providers.Web3Provider(window.ethereum);
      let signer = provider.getSigner();
      let account = await provider.send("eth_requestAccounts",[])
      setAccount(account[0])
      let address = "0x3Bd400cb5996DD763C59057BA2a85d3CB78Cc6d2";
      let abi = ["function createToken(string name,string  sym, uint totalsupply,uint decimal,address wallet)"]
      let contract = new ethers.Contract(address,abi,signer);
       let tok = await contract.createToken(token.tokenName,token.tokenSym,token.tokenSupply,7,account[0]);
      await tok.wait();
    }
  }
}
    }

   
     //getting token address
    let getID = async(event)=>{
event.preventDefault()
      let provider = new ethers.providers.Web3Provider(window.ethereum);
      let account = await provider.send("eth_requestAccounts",[])
      let address = "0x3Bd400cb5996DD763C59057BA2a85d3CB78Cc6d2";
      let abi = ["function getMyID(address signer) view returns(uint)",
      "function getAddress(uint index) view returns(address)"]
      let contract = new ethers.Contract(address,abi,provider)
      let ID = await contract.getMyID(account[0]);
        let tokenAddress = await contract.getAddress(ID)
        setAddress(tokenAddress)
}
    return(
        <>
        <div className="container-md bg">
          
    <form>
 <div className="mb-3">
 <label for="tokenname" className="form-label">set your token name:</label>
 <input type="text" id="tokenname" className="form-control" placeholder="@example:Bitcoin" required onChange={(event)=>{
   setToken((prev)=>{
    return {...prev,tokenName:event.target.value}
   })
 }}/>
 <p className="form-text">given input is act as a token name</p>
 </div>
<div className="mb-3">
<label className="form-label" for="tokensym">set your token symbol:</label>
 <input type="text" id="tokensym" className="form-control" required placeholder="@example:BTC" onChange={(event)=>{
   setToken((prev)=>{
    return {...prev,tokenSym:event.target.value}
   })
 }}/>
 <p className="form-text">given input is act as a token symbol</p>
</div>
 <div className="mb-3">
 <label className="form-label" for="tokensupply">set your token total supply</label>
 <input type="number" id="tokensupply" className="form-control" required placeholder="@example:10000" onChange={(event)=>{
   setToken((prev)=>{
    return {...prev,tokenSupply:event.target.value}
   })
 }}/>
 <p className="form-text">given input is act as a token supply</p>
 </div>
 <br />
 <i className="text-danger form-text">here 7 decimal is take as a default you need to add seven zero with your totalsupply eg: totalsupply+0000000*</i>
 <br />
 <br />
<div className="mb-2">
<button onClick={tokenmaker} className="btn btn-primary">make my coin</button>
 <input type="reset" className="btn btn-danger ms-2"/>
 <button onClick={getID} className="btn btn-success ms-2">getID</button>
</div>
    </form>
  <small className="text-wrap">this token address show on based your account ({account}) : {address}</small>
        </div>

        </>
    )
}